"use client";
import { Radar, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, ResponsiveContainer } from "recharts";

export default function StudentRadar({ student }) {
  if (!student) return <p>Select a student to view profile</p>;

  const chartData = [
    { skill: "Comprehension", value: student.comprehension },
    { skill: "Attention", value: student.attention },
    { skill: "Focus", value: student.focus },
    { skill: "Retention", value: student.retention },
    { skill: "Engagement", value: student.engagement_time },
  ];

  return (
    <ResponsiveContainer width="100%" height={300}>
      <RadarChart cx="50%" cy="50%" outerRadius="80%" data={chartData}>
        <PolarGrid />
        <PolarAngleAxis dataKey="skill" />
        <PolarRadiusAxis />
        <Radar name="Profile" dataKey="value" stroke="#82ca9d" fill="#82ca9d" fillOpacity={0.6} />
      </RadarChart>
    </ResponsiveContainer>
  );
}
