"use client";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";

export default function SkillBar({ data }) {
  // Aggregate average score by class for example
  const grouped = {};
  data.forEach(d => {
    if (!grouped[d.class]) grouped[d.class] = [];
    grouped[d.class].push(d.assessment_score);
  });

  const chartData = Object.entries(grouped).map(([cls, scores]) => ({
    class: cls,
    avg_score: (scores.reduce((a, b) => a + b, 0) / scores.length).toFixed(1),
  }));

  return (
    <ResponsiveContainer width="100%" height={300}>
      <BarChart data={chartData}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="class" />
        <YAxis />
        <Tooltip />
        <Bar dataKey="avg_score" fill="#82ca9d" />
      </BarChart>
    </ResponsiveContainer>
  );
}
