"use client";
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";

export default function AttentionScatter({ data }) {
  const chartData = data.map(d => ({
    attention: d.attention,
    score: d.assessment_score,
  }));

  return (
    <ResponsiveContainer width="100%" height={300}>
      <ScatterChart>
        <CartesianGrid />
        <XAxis type="number" dataKey="attention" name="Attention" />
        <YAxis type="number" dataKey="score" name="Assessment Score" />
        <Tooltip cursor={{ strokeDasharray: "3 3" }} />
        <Scatter name="Students" data={chartData} fill="#8884d8" />
      </ScatterChart>
    </ResponsiveContainer>
  );
}
